import { MessagesError, Util } from 'src/app/utilities/util';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { filterObject } from '../../models/filterObject';
import { ControlContainer } from '@angular/forms';
import { Privilegios } from 'src/app/models/login';
import { Menu, NavItem } from 'src/app/models/privilegiosConf';
import { PrivilegiosServiceService } from 'src/app/service/privilegios-service.service';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html', 
  styleUrls: ['./filter.component.css']
})

export class FilterComponent implements OnInit {

  model: NgbDateStruct;
  // Lista de objetos tipo filterObject el cual contiene
  // los datos para generar los Input necesarios para el filtro;
  @Input() filter: filterObject[] = [];

  // Id del filtro
  @Input() idFilter: string = 'filter';

  // Id del filtro
  @Input() data: any = [];

  //Lista que regresa un arreglo
  //con el JSON = {key: id del filterObject , Value : valor puesto en el input correspondiende al id del filterObjecto}
  @Output() getValues: any = new EventEmitter<any>();

  @Output() visibleAdd: any = new EventEmitter<any>();

  private util = new Util();
  messages: MessagesError = new MessagesError();
  visible: any = false;
  values: any = [];
  validation: any = [];
  flag: any=[];
  arrPrivilegios :Array<Privilegios>;
  menuItemAdmin:Array<Menu>=[]
  navItem:Array<NavItem> = [];
  constructor(private privilegiosService: PrivilegiosServiceService) { }
  namebuttom: String = "";
  save: any = {
    guardar: false
  };

  ngOnInit(): void {
    this.values = new Array(this.filter.length);
    this.values.fill('', 0, this.filter.length);
    this.validation = new Array(this.filter.length);
    this.validation.fill(true, 0, this.filter.length);

    this.privilegiosService.getPrivilegios().subscribe((data)=>{
      this.arrPrivilegios = data;
      
    });
    this.privilegiosService.setPrivilegios();

    let util = new Util();
    util.tree(this.arrPrivilegios,0)

    let temp = this.arrPrivilegios.filter((menuP)=>{
      return menuP.parentprivilegio == 102 && menuP.status == 1 && menuP.idprivilegio == 260
      // menuP.descripcion.includes('Menu principal')
    })
      this.namebuttom = temp[0].etiqueta;
  }

  getValuesAction() {
    let array = [];
    let i = 0;
    let existe_error = false;
    for (const item of this.filter) {
      let value = '';
      if (item.type == 'date') {
        if (this.values[i] != ' ') {
          // value =  this.util.formatDateToStringFormat(this.values[i]);
          value = this.values[i];
        }
      } else {
        value = this.values[i];
      }
      array.push({ id: item.id, value: value });   
      i++;
    }
    this.validation.forEach(element => {
      if (element == false) {
        existe_error = true;
      }
    });
    if (!existe_error) {
      this.getValues.emit(array);
    } else {
      this.util.errorToast(this.messages._FILTER);
    }
    this.flag = this.save.guardar
    console.log(this.flag);
  }
  


  clean() {
    let elements: any = document.querySelectorAll("select");
    for (let i = 0; i < elements.length; i++) {
      let option: any = elements[i].querySelectorAll("option");
      elements[i].value = option[0].value;
      console.log(elements[i]);
      
    }
    this.values.fill('', 0, this.filter.length);
    this.validation.fill(true, 0, this.filter.length);
    this.getValuesAction();
  }

  unfocus(type: any, i: any, a: any) {
    if (type == 'date') {
      if (this.values[i] == '') {
        this.values[i] = ' ';
      }
      else if (this.values[i] == ' ') {
        this.values[i] = '';
      }
    }
  }

  getVisible() {
    if (this.visible == true) {
      this.visible = false;
      this.visibleAdd.emit(true);
      this.clean();
    } else {
      this.visible = true;
      this.visibleAdd.emit(false);
    }
  }

  filterList(array: any) {
    let list = this.data.filter(function (obj: any) {
      if (compare(obj, array)
      ) {
        return true;
      } else {
        return false;
      }
    });
    return list;
  }


  isList(type: any) {
    return type == 'list';
  }


  getSelectedValue(a: any, i: any) {
    this.values[i] = a;
  }
  
  set(a: any, i: any) {
    this.values[i] = a;
  }
  
  setListValue(entry: any, keyList: any) {
    if (keyList != null) {
      const arrayObj = Object.entries(entry);
      for (const entryObj of arrayObj) {
        const key = entryObj[0];
        const value = entryObj[1];
        if (key == keyList) {
          return value;
        }
      }
    } else { return entry }

  }

  icon(icon: string) {
    if (icon == null || icon == '') {
      return false;
    }
    else {
      return true;
    }
  }

  pattern(text: string, index: any, pattern: any) {
    if (pattern == null) { pattern = '.*' }
    const valid = text.match(pattern);
    if (text == '') {
      this.validation[index] = true;
      return;
    }
    if (valid != null) {
      if (valid[0].length != text.length) {
        this.validation[index] = false;
      } else { this.validation[index] = true }
    } else { this.validation[index] = false }
  }
}

function compare(obj: any, array: any) {
  let accept: any = [];
  array.forEach((element: any) => {
    Object.entries(obj).forEach(([key, value]) => {
      if (typeof value !== 'object' || value == null) {
        if (key == element.id) {
          if ((('' + value).toLocaleLowerCase()).includes(element.value.toLocaleLowerCase())) {
            accept.push(true);
          } else {
            accept.push(false);
          }
        }
      } else {
        const object = element.id.split('.');
        if (object[0] == key) {
          let exist = false;
          Object.entries(value).forEach(([keyObject, valueObject]) => {
            if (object[1] == keyObject) {
              exist = true;
              if ((('' + valueObject).toLocaleLowerCase()).includes(element.value.toLocaleLowerCase())) {
                accept.push(true);
              } else {
                accept.push(false);
              }
            }
          });
          if (!exist) {
            if ((element.value.toLocaleLowerCase() == '')) {
              accept.push(true);
            } else {
              accept.push(false);
            }
          }
        }
      }

    });
  });

  let fa = true;
  accept.forEach((entry: any) => {
    if (!entry) {
      fa = false;
    }
  });
  return fa;
  
}

