import { Component, Input, OnInit, OnChanges, SimpleChanges, DoCheck, Output, EventEmitter, ViewChildren, ElementRef, QueryList } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { Privilegios } from 'src/app/models/login';
import { Util } from '../../utilities/util';
import { PrivilegiosServiceService } from 'src/app/service/privilegios-service.service';
import { merge } from 'rxjs';
import { Router, RouterModule } from '@angular/router';

const  util = new Util();
@Component({
  selector: 'app-table-component',
  templateUrl: './table-component.component.html',
  styleUrls: ['./table-component.component.css']
})
export class TableComponentComponent implements OnInit, OnChanges, DoCheck {
  p: number = 1;
  arrPrivilegios :Array<Privilegios>;
  arrayIcons:Array<string>=[];
  getIcons = [];
  masterSelected = false;
  checkedList:any;
  @Input()
  url:any;

  @Input()
  paginado = 10;
  //
  @Input()
  tableFor:string;  

  @Input()
  th_rows: Array<string> = [];

  @Input()
  filas: Array<any> = [];

  @Input()
  columnas: Array<string> = [];
  private arrKeysObject:Array<any>=[];

  @Output() 
  emitData = new EventEmitter<any>();

  @Output() 
  emitArrayData = new EventEmitter<any>();

  @Output() 
  emitEventData = new EventEmitter<any>();
  
  copiaFilas : Array<any> = [];
  // public showRows = 
  ArrayKeys: Array<any> = [];
  data = [];

  @ViewChildren('checkBox') 
  viewChildren: QueryList<ElementRef>;

  setCheckBox = new Set<any>();

  constructor(private privilegiosService : PrivilegiosServiceService) { }
  
  private showData;
  
  ngDoCheck(): void {

    // throw new Error('Method not implemented.');
  }


  @Input() set setData(valueParam:Array<any>){
    let tamnaio = 0;
    let nuevoArr = []
    nuevoArr.length = 0
    let myKeys = [];
    const mySet1 = new Set();
    for(let i = 0;i<valueParam.length;i++){
      if(Object.keys(valueParam[i]).length >= tamnaio){
        myKeys.length = 0
        tamnaio = Object.keys(valueParam[i]).length;
        myKeys = Object.keys(valueParam[i])
      }
    }
    
    for(let i = 0; i < valueParam.length; i++){
      let obj = this.defineObject(myKeys)
      if(typeof valueParam[i] === 'object'){
        for(let j = 0 ; j < myKeys.length; j++){
          if(valueParam[i][myKeys[j]] == undefined){
            obj[myKeys[j]] = null;  
          }else{
            obj[myKeys[j]] = valueParam[i][myKeys[j]]
          }
        }
        Reflect.set(obj, 'isSelected', false);
        nuevoArr.push(obj)
        mySet1.add(obj)
      }
    }
    // console.table(nuevoArr);
    
    this.showData = nuevoArr;
  }
  get getData(){
    return this.showData;
  }

  evaluateTitle(_param:string){
    return _param.toLowerCase().includes('iduser') ? true : false;
  }
  
  ngOnChanges(changes: SimpleChanges): void {
    if(changes['filas']){

    }
    this.copiaFilas = [...this.filas]
    this.generateObjectGeneric()
  }

  generateObjectGeneric(){
    
    let tamnaio = 0;
    for(let i = 0;i<this.copiaFilas.length;i++){
      if(Object.keys(this.copiaFilas[i]).length >= tamnaio){
        this.ArrayKeys.length = 0
        tamnaio = Object.keys(this.copiaFilas[i]).length;
        this.ArrayKeys = Object.keys(this.copiaFilas[i])
      }
    }

    this.ArrayKeys.forEach((llave) =>{
      for(let i = 0; i < this.copiaFilas.length; i++){
        if(typeof this.copiaFilas[i] === 'object'){
          this.copiaFilas[i] = Reflect.set(this.copiaFilas[i], 'isSelect', true);
          if(Reflect.has(this.copiaFilas[i], llave) != true){
            this.copiaFilas[i] = Reflect.set(this.copiaFilas[i], llave, "N/A")
            console.log(this.copiaFilas);
          }
        }
      }
    })
  }

  ngOnInit(): void {
    this.privilegiosService.getPrivilegios().subscribe((data)=>{
      this.arrPrivilegios = data;
    });
    this.privilegiosService.setPrivilegios();
    if(this.tableFor == "users"){
      this.configUsersGrid();
    } else if (this.tableFor == "solicitudes"){
      this.configSolicitudesGrid();
    }
  }

  configUsersGrid(){ 
    util.tree(this.arrPrivilegios,0)
    this.arrPrivilegios.filter((menuP)=>{
      return menuP.parentprivilegio == 0 && menuP.status == 1 && menuP.descripcion.includes('Menu principal')
    })
    let temporal = util.tree(this.arrPrivilegios,0)
    let subMenus = this.searchTree(temporal, "Usuarios");
    this.setIcons(subMenus.children)

    setTimeout(()=>{
      let temp = this.privilegiosService.getAllPrivilegiosArr();      
      // console.log(temp);
      this.getIcons =  new Privilegios(this.privilegiosService.searchTree(temp[1], 102)).children
      // console.log(this.getIcons);
      
    },5000)
  }

  configSolicitudesGrid(){  
    util.tree(this.arrPrivilegios,0)
    this.arrPrivilegios.filter((menuP)=>{
      // console.log(menuP);
      
      return menuP.parentprivilegio == 0 && menuP.status == 1 && menuP.descripcion.includes('Solicitudes')
    })
    let temporal = util.tree(this.arrPrivilegios,0)
    let subMenus = this.searchTree(temporal, "Solicitudes");
    this.setIcons(subMenus.children)

    setTimeout(()=>{
      let temp = this.privilegiosService.getAllPrivilegiosArr();      
      // console.log(temp);
      this.getIcons =  new Privilegios(this.privilegiosService.searchTree(temp[1], 254)).children
      // console.log(this.getIcons);
      
    },5000)
  }

  setIcons(subMenus:Array<any>){
    subMenus.forEach((data)=> {
      if(data.link.includes("fa") && data.status == 1){
        this.arrayIcons.push(data.link)
      }
    })
  }

  searchTree(element, matchingTitle) {
    if (element.descripcion == matchingTitle) {
      return element;
    } else if (element.children != null) {
      let i;
      let result = null;
      for (i = 0; result == null && i < element.children.length; i++) {
        result = this.searchTree(element.children[i], matchingTitle);
      }
      return result;
    }
    return null;
  }

  destructurinData(_param):Array<any>{
    let arr = [];
    for (let value of Object.values(_param)) {
      arr.push(value);
    }
    return arr;
  }

  keys(param){
    let arr = [];
    const me = Object.create(param);
    let entriesData = Object.entries(param);
    for (let [key, value] of Object.entries(param)) {
      arr.push(key);
    }
    return arr;
  }

  /*
    configuracion de tabla
  */
  getKeys(_array:Array<any>){
    this.arrKeysObject.length = 0;
    for(let element of _array){
      if(this.th_rows.length == Object.keys(element).length )
        this.arrKeysObject =Object.keys(element)    
      break;  
      } 
  }


  defineObject(_keys: Array<string>) {
    let normalObj = Object.create(null)
    _keys.forEach((element) => {
      Object.defineProperty(normalObj, element, {
        configurable: true,
        enumerable: true,
        writable: true,
        value: null
      })
    })
    return normalObj;
  }

  capitalizeFirstLetter(_param:string) {
    let transformString = _param.toLowerCase()
    return transformString.charAt(0).toUpperCase() + transformString.slice(1);
  }

  doubleClick(e:any){
    this.emitData.emit(e);
  }

  /*
    selected and unselected
  */
  checkUncheckAll() {
    for (let i = 0; i < this.showData.length; i++) {
      this.showData[i].isSelected = this.masterSelected;
    }
    this.getCheckedItemList();
  }

  // Check All Checkbox Checked
  isAllSelected() {
    this.masterSelected = this.showData.every(function(item:any) {
        return item.isSelected == true;
      })
    this.getCheckedItemList();
  }

  // Get List of Checked Items
  getCheckedItemList(){
    this.checkedList = [];
    for (let i = 0; i < this.showData.length; i++) {
      if( this.showData[i].isSelected)
        this.checkedList.push(this.showData[i]);
    }
    this.emitArrayData.emit(this.checkedList);
    this.checkedList = JSON.stringify(this.checkedList);

    // console.log(this.checkedList);
    
  }

  iconData(event){
    this.emitEventData.emit(event)
  }

}

